import React from "react";
import { NavLink } from "react-router-dom";

import { Sidebar, Menu, MenuItem, SubMenu } from "react-pro-sidebar";
import "./Sidebar.css";

const SidebarMenu = () => {
  const styleActive = ({ isActive }) => ({
    backgroundColor: isActive ? "rgb(0, 114, 188)" : "",
    borderRadius: "10px",
  });

  return (
    <div
      className="div-sidebar"
      style={{
        width: "310px",
      }}
    >
      <Sidebar
        width="310px"
        rootStyles={{
          borderRadius: "10px",
        }}
      >
        <Menu
          menuItemStyles={{
            button: ({ level, active }) => {
              if (level === 0 || level === 1 || level === 2) {
                return {
                  backgroundColor: active ? "rgb(0, 114, 188)" : undefined,
                  borderRadius: "10px",
                  "&:hover": {
                    backgroundColor: "rgb(0, 114, 188)",
                    color: "white",
                  },
                };
              }
            },
          }}
        >
          <MenuItem component={<NavLink to="/circuit" style={styleActive} />}>
            
          </MenuItem>
          <SubMenu label="">
            <MenuItem
              component={
                <NavLink to="/lectures/quantum_bit" style={styleActive} />
              }
            >
               
            </MenuItem>
            <MenuItem
              component={
                <NavLink to="/lectures/quantum_system" style={styleActive} />
              }
            >
               
            </MenuItem>
            <MenuItem
              component={
                <NavLink
                  to="/lectures/measurement_in_quantum_system"
                  style={styleActive}
                />
              }
            >
                
            </MenuItem>
            <SubMenu label=" ">
              <MenuItem
                component={
                  <NavLink to="/lectures/hadamard_gate" style={styleActive} />
                }
              >
                 
              </MenuItem>
              <MenuItem
                component={
                  <NavLink to="/lectures/X_gate" style={styleActive} />
                }
              >
                 NOT ( X)
              </MenuItem>
              <MenuItem
                component={
                  <NavLink to="/lectures/Z_gate" style={styleActive} />
                }
              >
                 Z
              </MenuItem>
              <MenuItem
                component={
                  <NavLink to="/lectures/Y_gate" style={styleActive} />
                }
              >
                 Y
              </MenuItem>
              <MenuItem
                component={
                  <NavLink to="/lectures/S_gate" style={styleActive} />
                }
              >
                 S
              </MenuItem>
              <MenuItem
                component={
                  <NavLink to="/lectures/T_gate" style={styleActive} />
                }
              >
                 T
              </MenuItem>
              <MenuItem
                component={
                  <NavLink to="/lectures/RxRyRz_gate" style={styleActive} />
                }
              >
                  Rx, Ry, Rz
              </MenuItem>
              <MenuItem
                component={
                  <NavLink to="/lectures/U_gates" style={styleActive} />
                }
              >
                <div> </div>
                <div>  U</div>
              </MenuItem>
              <MenuItem
                component={
                  <NavLink to="/lectures/table_gates" style={styleActive} />
                }
              >
                <div> </div>
                <div></div>
              </MenuItem>
              <MenuItem
                component={
                  <NavLink
                    to="/lectures/controlled_gates"
                    style={styleActive}
                  />
                }
              >
                 
              </MenuItem>
            </SubMenu>
            <SubMenu label=" ">
              <MenuItem
                component={
                  <NavLink to="/lectures/Bell_state" style={styleActive} />
                }
              >
                {" "}
                <div> </div>
                <div>  .  </div>
              </MenuItem>
              <MenuItem
                component={
                  <NavLink
                    to="/lectures/superdense_coding"
                    style={styleActive}
                  />
                }
              >
                {" "}
                 
              </MenuItem>
              <MenuItem
                component={
                  <NavLink
                    to="/lectures/quantum_teleportation"
                    style={styleActive}
                  />
                }
              >
                {" "}
                 
              </MenuItem>
              <MenuItem
                component={
                  <NavLink
                    to="/lectures/DeutschJozsa_algorithm"
                    style={styleActive}
                  />
                }
              >
                {" "}
                <div> .</div>
                <div> -</div>
              </MenuItem>
              <MenuItem
                component={
                  <NavLink
                    to="/lectures/Grover's_algorithm"
                    style={styleActive}
                  />
                }
              >
                {" "}
                 
              </MenuItem>
              <MenuItem
                component={
                  <NavLink
                    to="/lectures/quantum_fourier_transform"
                    style={styleActive}
                  />
                }
              >
                {" "}
                <div></div>
                <div> </div>
              </MenuItem>
              <MenuItem
                component={
                  <NavLink
                    to="/lectures/Shor's_algorithm"
                    style={styleActive}
                  />
                }
              >
                {" "}
                 
              </MenuItem>
            </SubMenu>
          </SubMenu>
          <SubMenu label="">
            <MenuItem component={
                <NavLink
                  to="/practice/practice1"
                  style={styleActive}
                  />
              }>  1</MenuItem>
            <MenuItem component={
                <NavLink
                  to="/practice/practice2"
                  style={styleActive}
                  />
                   
            }>  2</MenuItem>
            <MenuItem component={
                <NavLink
                  to="/practice/practice3"
                  style={styleActive}
                  /> 
            }>  3</MenuItem>
            <MenuItem component={
                <NavLink
                  to="/practice/practice4"
                  style={styleActive}
                  /> 
            }>  4</MenuItem>
          </SubMenu>
          <SubMenu label=''>
            <MenuItem component={
              <NavLink
                to="/files/"
                style={styleActive}
                />
            }>PDF-file</MenuItem>
            <MenuItem component={
              <NavLink
                to="/files/"
                style={styleActive}
                />
            }></MenuItem>
            <MenuItem component={
              <NavLink
                to="/files/"
                style={styleActive}
                />
            }>_1</MenuItem>
          </SubMenu>
        </Menu>
      </Sidebar>
    </div>
  );
};

export default SidebarMenu;
